const POST_QUERY = graphql`

    query GETPOST {
        wpgraphql {
            posts(first: 6, where: {categoryId: 3}) {
                nodes {
                    title
                    excerpt
                    uri
                    featuredImage {
                        altText
                        sourceUrl
                        imageFile {
                            childImageSharp {
                                fluid(maxHeight: 600, maxWidth: 1000, quality: 100, cropFocus: CENTER) {
                                    ...GatsbyImageSharpFluid_tracedSVG
                                }
                            }
                        }
                    }
                    CustomPost {
                        acreage
                        address
                        bedroom
                        fieldGroupName
                        price
                        wc
                    }
                }
            }
        }
    }
`
const FeaturedCategory = ({ title }) => {
    // console.log(excerpt);
    return (
        <section className="featured-category pt-4">
            <div className="container">
                <div className="row">
                    <div className="title text-center w-100">
                        <h2>{title}</h2>
                    </div>
                    <div className="list-post w-100">
                        <div className="row">
                            <StaticQuery
                                query={POST_QUERY}
                                render={(data) => {

                                    if (data.wpgraphql.posts) {
                                        const posts = data.wpgraphql.posts.nodes

                                        return (
                                            <>
                                                {
                                                    posts &&
                                                    posts.map((prop, index) => (

                                                        <div className="col-md-4 col-xs-12" key={index}>
                                                            <article className="item">
                                                                <div className="featured-image">
                                                                    <Link to={`${blogURI}/${prop.uri}/`}>
                                                                        <div className="ov1"></div>
                                                                        <div className="ov2"></div>
                                                                        <div className="ov3"></div>
                                                                        <div className="ov4"></div>
                                                                        <FluidImage image={prop.featuredImage} alt={prop.title} />
                                                                    </Link>
                                                                </div>
                                                                <div className="title">
                                                                    <Link to={`${blogURI}/${prop.uri}/`} title={prop.title}>
                                                                        <h3>{prop.title}</h3>
                                                                    </Link>
                                                                </div>
                                                                <div className="excerpt">
                                                                    <div className="address">
                                                                       <span><i className="fa fa-map-marker" aria-hidden="true"></i>{prop.CustomPost.address}</span> 
                                                                    </div>
                                                                    <div className="price">
                                                                        <span>Giá: ~ {prop.CustomPost.price}</span>
                                                                    </div>
                                                                    <div className="d-flex justify-content-between">
                                                                        <div>{prop.CustomPost.bedroom}</div>
                                                                        <div>{prop.CustomPost.wc}</div>
                                                                        <div>{prop.CustomPost.acreage}</div>
                                                                    </div>
                                                                </div>
                                                            </article>
                                                        </div>
                                                    ))
                                                }
                                            </>
                                        )
                                    }
                                    return null
                                }}
                            />
                        </div>
                    </div>
                    {/* {
                        category &&
                        category.map((prop, index) => (
                            <div key={index}>
                                <h3>{prop.termTaxonomyId}</h3>
                            </div>
                        ))
                    } */}
                </div>
            </div>
        </section>
    )
}

export default FeaturedCategory


